import React from "react";
import {Header} from "./partial/header/Header";
import {Hero} from "./partial/hero/Hero";
import {AboutUs} from "./partial/aboutus/AboutUs";

function App() {
    return (
        <>
            <Header/>
            <Hero/>
            <AboutUs/>
            <footer>
                <div className="footer-main">
                    <div className="row">
                        <div className="col-three md-1-3 tab-full footer-info">
                            <ul className="footer-social-list">
                                <li>
                                    <a href="#"><i className="fa fa-dribbble"></i></a>
                                </li>
                                <li>
                                    <a href="#"><i className="fa fa-instagram"></i></a>
                                </li>
                            </ul>
                        </div>
                        <div className="col-three md-1-3 tab-1-2 mob-full footer-contact">
                            <h4>Contact</h4>
                            <p>

                            </p>
                        </div>
                    </div>
                </div>
                <div className="footer-bottom">
                    <div className="row">
                        <div className="col-twelve">
                            <div className="copyright">
                                <span>© Copyright Toussaint - 2021.</span>
                            </div>
                        </div>
                    </div>
                </div>
            </footer>
        </>
    );
}

export default App;
